import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  //	mode: 'history',
  base: __dirname,
  linkActiveClass: 'active',
  routes: [
    //登录
    {
      path: '/loginRegister',
      name: 'LoginRegister',
      component: resolve => require(['@/pages/Register/LoginRegister'], resolve)
    },
    //注册
    {
      path: '/register',
      name: 'Register',
      component: resolve => require(['@/pages/Register/Register'], resolve)
    },
    //完善信息
    {
      path: '/improveInformation',
      name: 'ImproveInformation',
      component: resolve => require(['@/pages/Register/ImproveInformation'], resolve)
    },
    //完善手机
    {
      path: '/improvePhone',
      name: 'ImprovePhone',
      component: resolve => require(['@/pages/Register/ImprovePhone'], resolve)
    },
    {
      path: '/',
      name: 'Main',
      redirect: {
        name: 'Index'
      },
      component: resolve => require(['@/pages/Main'], resolve),
      children: [{
          path: 'index',
          name: 'Index',
          component: resolve => require(['@/pages/Index/Index'], resolve)
        },
        {
          path: 'chooseGift',
          name: 'ChooseGift',
          component: resolve => require(['@/pages/ChooseGift/ChooseGift'], resolve)
        },
        {
          path: 'mine',
          name: 'Mine',
          component: resolve => require(['@/pages/Mine/Mine'], resolve)
        }
      ]
    },
    {
      path: '/sendGift',
      name: 'SendGift',
      component: resolve => require(['@/pages/SendGift/SendGift'], resolve)
    },
    {
      path: '/sendGiftList',
      name: 'SendGiftList',
      component: resolve => require(['@/pages/SendGift/SendGiftList'], resolve)
    },
    {
      path: '/goodsList',
      name: 'GoodsList',
      component: resolve => require(['@/pages/GoodsList/GoodsList'], resolve)
    },
    {
      path: '/shopGoodsList',
      name: 'ShopGoodsList',
      component: resolve => require(['@/pages/GoodsList/ShopGoodsList'], resolve)
    },
    {
      path: '/chooseActivity',
      name: 'ChooseActivity',
      component: resolve => require(['@/pages/ChooseActivity/ChooseActivity'], resolve)
    },
    //新人专区
//  {
//    path: '/newcomer',
//    name: 'Newcomer',
//    component: resolve => require(['@/pages/Index/Newcomer/Newcomer'], resolve)
//  },
    //系统公告
    {
      path: '/notice',
      name: 'Notice',
      component: resolve => require(['@/pages/Index/Notice/Notice'], resolve)
    },
    //购物车
    {
      path: '/cart',
      name: 'Cart',
      component: resolve => require(['@/pages/Cart/Cart'], resolve)
    },
    //确认订单
    {
      path: '/confirmOrder',
      name: 'ConfirmOrder',
      component: resolve => require(['@/pages/Cart/ConfirmOrder'], resolve)
    },
    //订单选择优惠券
    {
      path: '/confirmOrder/coupon',
      name: 'ConfirmCoupon',
      component: resolve => require(['@/pages/Coupon/Coupon'], resolve)
    },
    //订单选择地址
    {
      path: '/confirmOrder/address',
      name: 'ConfirmAddress',
      component: resolve => require(['@/pages/Address/Index'], resolve)
    },
    //订单地址管理
    {
      path: '/confirmOrder/addressManage',
      name: 'ConfirmAddMan',
      component: resolve => require(['@/pages/Address/Manage'], resolve)
    },
    //收银台
    {
      path: '/cashier',
      name: 'Cashier',
      component: resolve => require(['@/pages/Cashier/Cashier'], resolve)
    },
    //支付成功
    {
      path: '/paySuccess',
      name: 'PaySuccess',
      component: resolve => require(['@/pages/PaySuccess/PaySuccess'], resolve)
    },

    /*收货地址管理 start*/
    // 收货地址列表
    {
      path: '/mine/address',
      name: 'Address',
      component: resolve => require(['@/pages/Address/Index'], resolve)
    },
    {
      path: '/mine/addressManage',
      name: 'AddressManage',
      component: resolve => require(['@/pages/Address/Manage'], resolve)
    },
    {
      path: '/mine/addressEdit',
      name: 'AddressEdit',
      component: resolve => require(['@/pages/Address/Manage'], resolve)
    },
    /*收货地址管理 end*/
    /*商品详情 start*/
    {
      path: '/goodDetail',
      name: 'GoodDetail',
      component: resolve => require(['@/pages/GoodDetail/GoodDetail'], resolve)
    },
    //评价列表
    {
      path: '/commentList/:id',
      name: 'CommentList',
      component: resolve => require(['@/pages/CommentList/CommentList'], resolve)
    },
    //优惠券
    {
      path: '/mine/coupon',
      name: 'Coupon',
      component: resolve => require(['@/pages/Coupon/Coupon'], resolve)
    },
    {
      path: '/mine/coupon/addCoupon',
      name: 'AddCoupon',
      component: resolve => require(['@/pages/Coupon/AddCoupon'], resolve)
    },
    /*商品详情 end*/
    /*定制 start*/
    {
      path: '/customized',
      name: 'Customized',
      component: resolve => require(['@/pages/Customized/Index'], resolve)
    },
    {
      path: '/customizedStoreList',
      name: 'CustomizedStoreList',
      component: resolve => require(['@/pages/Customized/StoreList'], resolve)
    },
    {
      path: '/customizedExplain',
      name: 'CustomizedExplain',
      component: resolve => require(['@/pages/Customized/Explain'], resolve)
    },
    {
      path: '/customizedEdit',
      name: 'CustomizedEdit',
      component: resolve => require(['@/pages/Customized/Edit'], resolve)
    },
    {
      path: '/customizedGoodsList',
      name: 'CustomizedGoodsList',
      component: resolve => require(['@/pages/Customized/GoodsList'], resolve)
    },
    {
      path: '/customizedBox',
      name: 'CustomizedBox',
      component: resolve => require(['@/pages/Customized/CustomizedBox'], resolve)
    },
    {
      path: '/customizedConfirmStep1',
      name: 'CustomizedConfirmStep1',
      component: resolve => require(['@/pages/Customized/Confirm'], resolve)
    },
    {
      path: '/customizedSeach',
      name: 'CustomizedSeach',
      component: resolve => require(['@/pages/Customized/CustomizedSeach'], resolve)
    },
    {
      path: '/customizedGoodsDeatail',
      name: 'CustomizedGoodsDeatail',
      component: resolve => require(['@/pages/GoodDetail/GoodDetail'], resolve)
    },
   
    /*定制 end*/
    //订单列表
    {
      path: '/mine/orderList',
      name: 'OrderList',
      component: resolve => require(['@/pages/MyOrderList/MyOrderList'], resolve)
    },
    {
      path: '/mine/orderList/orderDetail/:id',
      name: 'OrderDetail',
      component: resolve => require(['@/pages/MyOrderList/OrderDetail'], resolve)
    },
    {
      path: '/selectGoods/:id',
      name: 'SelectGoods',
      component: resolve => require(['@/pages/MyOrderList/SelectGoods'], resolve)
    },
    {
      path: '/publish/:id',
      name: 'Publish',
      component: resolve => require(['@/pages/MyOrderList/Publish'], resolve)
    },
    {
      path: '/afterSale',
      name: 'AfterSale',
      component: resolve => require(['@/pages/MyOrderList/AfterSale'], resolve)
    },
    //搜素页
    {
      path: '/search',
      name: 'Search',
      component: resolve => require(['@/pages/Search/Search'], resolve)
    },
    //个人-消息中心
    {
      path: '/mine/message',
      name: 'Message',
      component: resolve => require(['@/pages/Mine/Message/Message'], resolve)
    },
    //个人-个人资料编辑
    {
      path: '/mine/edit',
      name: 'Edit',
      component: resolve => require(['@/pages/Mine/Edit/Edit'], resolve)
    },
    //个人-礼物包
    {
      path: '/mine/gift',
      name: 'Gift',
      component: resolve => require(['@/pages/Mine/Gift/Gift'], resolve)
    },
    //个人-礼物包-礼物详情
    {
      path: '/GiftDetailsPage',
      name: 'GiftDetail',
      component: resolve => require(['@/pages/Mine/GiftDetail/GiftDetail'], resolve)
    },
    //个人-礼物包-礼物详情-转赠
    {
      path: '/mine/transmit',
      name: 'Transmit',
      component: resolve => require(['@/pages/Mine/Transmit/Transmit'], resolve)
    },
    //个人-礼物包-礼物详情-回收礼物
    {
      path: '/mine/recover',
      name: 'Recover',
      component: resolve => require(['@/pages/Mine/Recover/Recover'], resolve)
    },
    //个人-礼物包-礼物详情-回收礼物
    {
      path: '/mine/send',
      name: 'Send',
      component: resolve => require(['@/pages/Mine/Send/Send'], resolve)
    },
    //个人-礼物包-礼物记录
    {
      path: '/mine/giftData',
      name: 'GiftData',
      component: resolve => require(['@/pages/Mine/GiftData/GiftData'], resolve)
    },
    //个人-钱包
    {
      path: '/mine/wallet',
      name: 'Wallet',
      component: resolve => require(['@/pages/Mine/Wallet/Wallet'], resolve)
    },
    //个人-钱包
    {
      path: '/mine/walletData',
      name: 'WalletData',
      component: resolve => require(['@/pages/Mine/WalletData/WalletData'], resolve)
    },
    //个人-钱包-充值
    {
      path: '/mine/recharge',
      name: 'Recharge',
      component: resolve => require(['@/pages/Mine/Recharge/Recharge'], resolve)
    },
    //个人-钱包-提现
    {
      path: '/mine/withDraw',
      name: 'WithDraw',
      component: resolve => require(['@/pages/Mine/WithDraw/WithDraw'], resolve)
    },
    //个人-钱包-提现
    {
      path: '/mine/phone',
      name: 'Phone',
      component: resolve => require(['@/pages/Mine/Phone/Phone'], resolve)
    },
    //个人-钱包-提现-银行卡
    {
      path: '/mine/bankCard',
      name: 'BankCard',
      component: resolve => require(['@/pages/Mine/BankCard/BankCard'], resolve)
    },
    //个人-钱包-提现-银行卡-添加银行卡
    {
      path: '/mine/addBankCard',
      name: 'AddBankCard',
      component: resolve => require(['@/pages/Mine/AddBankCard/AddBankCard'], resolve)
    },
    //鸟蛋
    {
      path: '/mine/birdEgg',
      name: 'BirdEgg',
      component: resolve => require(['@/pages/Mine/BirdEgg/BirdEgg'], resolve)
    },
    //规则说明
    {
      path: '/articleDetails',
      name: 'ArticleDetails',
      component: resolve => require(['@/pages/ArticleDetails/ArticleDetails'], resolve)
    },
    //爱心
    {
      path: '/mine/loveheart',
      name: 'LoveHeart',
      component: resolve => require(['@/pages/Mine/LoveHeart/LoveHeart'], resolve)
    },
    {
      path: '/mine/loveheart/heartRecharge',
      name: 'HeartRecharge',
      component: resolve => require(['@/pages/Mine/LoveHeart/HeartRecharge'], resolve)
    },

    //设置
    {
      path: '/setting',
      name: 'Setting',
      component: resolve => require(['@/pages/Setting/Setting'], resolve)
    },
    //设置-账号安全
    {
      path: '/setting/account',
      name: 'Account',
      component: resolve => require(['@/pages/Setting/Account/Account'], resolve)
    },
    //设置-账号安全-手机账号
    {
      path: '/setting/code',
      name: 'Code',
      component: resolve => require(['@/pages/Setting/Code/Code'], resolve)
    },
    //设置-账号安全-手机账号-完成
    {
      path: '/setting/editPhone',
      name: 'EditPhone',
      component: resolve => require(['@/pages/Setting/EditPhone/EditPhone'], resolve)
    },
    //设置-账号安全-登录密码
    {
      path: '/setting/editPassword',
      name: 'EditPassword',
      component: resolve => require(['@/pages/Setting/EditPassword/EditPassword'], resolve)
    },
    //设置-账号安全-支付密码
    {
      path: '/setting/editPay',
      name: 'EditPay',
      component: resolve => require(['@/pages/Setting/EditPay/EditPay'], resolve)
    },
    //设置-意见反馈
    {
      path: '/setting/feedBack',
      name: 'FeedBack',
      component: resolve => require(['@/pages/Setting/FeedBack/FeedBack'], resolve)
    },
    //设置-关于
    {
      path: '/setting/about',
      name: 'About',
      component: resolve => require(['@/pages/Setting/About/About'], resolve)
    },
    {
      path: '/setting/aboutUs',
      name: 'AboutUs',
      component: resolve => require(['@/pages/Setting/AboutUs/AboutUs'], resolve)
    },
    {
      path: '/setting/user',
      name: 'User',
      component: resolve => require(['@/pages/Setting/User/User'], resolve)
    },
    {
      path: '/setting/game',
      name: 'Game',
      component: resolve => require(['@/pages/Setting/Game/Game'], resolve)
    },
    {
      path: '/setting/contact',
      name: 'Contact',
      component: resolve => require(['@/pages/Setting/Contact/Contact'], resolve)
    },
    {
      path: '/private/private',
      name: 'Private',
      component: resolve => require(['@/pages/private/private'], resolve)
    },
    {
      path: '/myAfterSales',
      name: 'MyAfterSales',
      component: resolve => require(['@/pages/Mine/myAfterSales/myAfterSales'], resolve)
    },
    {
      path: '/school',
      name: 'School',
      component: resolve => require(['@/pages/School/School'], resolve)
    },
    {
      path: '/school/apply',
      name: 'SchoolApply',
      component: resolve => require(['@/pages/School/Apply/Apply'], resolve)
    },
    {
      path: '/school/detail',
      name: 'SchoolDetail',
      component: resolve => require(['@/pages/School/Detail/Detail'], resolve)
    },
    {
      path: '/school/list',
      name: 'SchoolList',
      component: resolve => require(['@/pages/School/List/List'], resolve)
    },
    //推广员
    {
      path: '/promote',
      name: 'Promote',
      component: resolve => require(['@/pages/Mine/Promote/Promote'], resolve)
    },
    {
      path: '/promote/promoted',
      name: 'Promoted',
      component: resolve => require(['@/pages/Mine/Promote/Promoted/Promoted'], resolve)
    },
    {
      path: '/promote/promoters',
      name: 'Promoters',
      component: resolve => require(['@/pages/Mine/Promote/Promoters/Promoters'], resolve)
    },
    {
      path: '/promote/promoteInfo',
      name: 'PromoteInfo',
      component: resolve => require(['@/pages/Mine/Promote/PromoteInfo/PromoteInfo'], resolve)
    },
    {
      path: '/promote/promotePhone',
      name: 'PromotePhone',
      component: resolve => require(['@/pages/Mine/Promote/PromotePhone/PromotePhone'], resolve)
    },
  ],
  //对于所有路由导航，简单地让页面滚动到顶部。
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})



