  	loginInit(){
  		if(this.$wechat.isWeiXin()){
  			var url = location.search; //获取url中"?"符后的字串   
				var theRequest = new Object();   
				if (url.indexOf("?") != -1) {   
			    var str = url.substr(1);   
			    var strs = str.split("&");   
			    for(var i = 0; i < strs.length; i ++) {   
			      theRequest[strs[i].split("=")[0]]=unescape(strs[i].split("=")[1]);   
			    }   
				}
				let openId = theRequest.code;
				
				let session = localStorage.getItem('session')
				
  			if(session == null){
  				if(openId){
  					this.getUserPartnerLogin(openId)
  				}else{
//						console.log(oldUrl)
						if(process.env.NODE_ENV=='development'){
							return
						}else{
							let oldUrl = encodeURIComponent(window.location.href) 
							//微信授权重定向网址
							let newUrl = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx3ccfeeb07d15cf04&redirect_uri="+oldUrl+"&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect"
							window.location.href = newUrl
							//测试用
//							window.location.href = 'http://java1.d.aiitec.org/gift-fly/api/cis/redGetCode'
						}
  				}
  			}else{
  				this.getUserDetails()
  			}
  		}
