//  	判断用户是否已绑定手机
		getUserPartnerLogin(openId){
			let partner = 17
			if(localStorage.getItem('improvePhone')){
				return
			}else{
	  		this.$Api.getUserPartnerLogin(partner,openId).then((res)=>{
	  			if(res.q.s == 0){
						let SESSION = res.s;
	  				localStorage.setItem('session',SESSION)
						this.$Api.setSessionId(SESSION);
	  			}
	  		})
			}
		},
		getUserDetails() {
			this.$Api.UserDetails(0).then((res) => {
				if(res.q.s == '1012' || res.q.s == '1002'){
					localStorage.removeItem('session')
					let oldUrl = encodeURIComponent(window.location.href)
//					let newUrl = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx3ccfeeb07d15cf04&redirect_uri="+oldUrl+"&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect"
