import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick'
import axios from 'axios'
import Api from '@/api'
import wechat from '@/plugin/wechat'
import layer from '@/plugin/layer/layer'

import 'lib-flexible'

import COS from 'cos-js-sdk-v5'

import CommonHeader from '@/components/Header/CommonHeader'
import LoadingdMore from '@/components/LoadMore/LoadMore'

import './sass/public.scss'
import Vant from 'vant';
import 'vant/lib/vant-css/index.css';
import { Toast } from 'vant';
import { Loading } from 'vant';
import { InfiniteScroll } from 'mint-ui';//无限滚动
Vue.use(InfiniteScroll);
import { Loadmore } from 'mint-ui';

import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger.vue'
import vueTransform from 'css3transform/vue/transform.vue'

import { ImagePreview } from 'vant';

import call from '@/callApp.js';


Vue.use(Loading);
Vue.use(Vant);
Vue.use(layer);
Vue.use(AlloyFingerPlugin, {
    AlloyFinger
})

//全局定义头部
Vue.component('commonHeader', CommonHeader);

Vue.component('LoadMore', Loadmore);

Vue.component('LoadingdMore', LoadingdMore);
//全局方法
Vue.mixin({
  methods: {
    linkTo(name) {
      console.log(name);
      this.$router.push({name})
    },
    linkToUrl(url) {
      this.$router.push(url)
    },
    goBack(){
    	if(this.$route.query.mobile == 1){
      	backward({last:true})
	    }else{
	    	if(this.$route.query.shareBy || this.$route.query.fromShare == 1){
	    		this.$router.push({name:'Index'})
	    	}else{
	    		this.$router.back(-1)
	    	}
	    }
    },
    preview(images,index){
			let arr = images
			let imgArr = []
			arr.forEach((item)=>{
				imgArr.push(item.imagePath)
			})
			ImagePreview(imgArr,index);
		},
  }
})

axios.defaults.withCredentials = true
axios.defaults.crossDomain = true
//api.js wechat.js 绑定到vue实例上  可以this.调用
Vue.prototype.$Api = Api
Vue.prototype.$wechat = wechat
Vue.prototype.$Cos = COS
Vue.prototype.$call = call
Vue.prototype.globalValue = true
Vue.prototype.logined = false

//支付密码添加的key
Vue.prototype.$paykey = 'lwf15208355445155623a0Fn8ulvJxP3'


Vue.config.devtools = true

if ('addEventListener' in document) {
  document.addEventListener('DOMContentLoaded', function() {
      FastClick.attach(document.body);
  }, false);
}

Vue.config.productionTip = false



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  watch:{
    '$route':function(newval,oldval){
      // console.log(123)
      // console.log(oldval)
      // console.log(newval)
      // console.log(123)
      if(newval.name =='Search'&&oldval.name=='Customized' ){
        this.$route.query.id= 1
      }
      if(newval.name =='Search'&&oldval.name=='Search'){
        this.$route.query.id= 1
      }
      if(newval.name =='Search'&&oldval.name=='CustomizedStoreList' ){
        this.$route.query.id=1
      }
    }
  }
})



