import popup from './Popup';
import prompt from './Prompt';
import Api from '@/api'

var layer  = {};
var popupVm = null;
var promptVm = null;

layer.install = function(Vue, options) {

  Vue.prototype.$popup = ({showCancel = true, cancelText, sureText, title , content}) => {
    if(!popupVm) {
      let popupTpl = Vue.extend(popup);
      popupVm = new popupTpl();
      let tpl = popupVm.$mount().$el;
      document.body.appendChild(tpl)
    }

    popupVm.show = true;
    popupVm.showCancel = showCancel;

    if(cancelText) {
      popupVm.cancelText = cancelText;
    }else {
    	popupVm.cancelText = '取消';
    }

    if(sureText) {
      popupVm.sureText = sureText;
    }else{
    	popupVm.sureText = '确定';
    }
    
    if(title) {
      popupVm.title = title;
    }else{
      popupVm.title = '';
    }

    if(content) {
      popupVm.content = content;
    }else{
      popupVm.content = '';
    }

    return new Promise((resolve, reject) => {
      let sure = popupVm.sure;
      let cancel = popupVm.cancel;

      popupVm.sure = () => {
        sure();
        resolve('sure');
      }

      popupVm.cancel = () => {
        cancel();
        resolve('cancel');
      }
    })
  }

  let promptVmSure;    //获取初始化状态函数
  let promptVmCancel;  //获取初始化状态函数

  Vue.prototype.$prompt = () => {
    if(!promptVm) {
      let promptTpl = Vue.extend(prompt);
      promptVm = new promptTpl();
      console.log(promptVm)
      let tpl = promptVm.$mount().$el;
      document.body.appendChild(tpl)
      promptVmSure = promptVm.sure;
      promptVmCancel = promptVm.cancel;
    }

    promptVm.isShow = true;

    return new Promise((resolve, reject) => {

      promptVm.sure = () => {
        let val = promptVmSure();
        resolve({
          type: 'success',
          val
        });

        return val;
      }

      promptVm.cancel = () => {
        promptVmCancel();
        resolve({
          type: 'cancel',
          val: ''
        });
      }
    })
  }
  
  
  Vue.prototype.$upLoadToCos = (url,fileObj) =>{
  	var xhr = new XMLHttpRequest();
				xhr.open('PUT', url, true);
				xhr.onload = function() {
					if(xhr.status === 200 || xhr.status === 206) {
						var ETag = xhr.getResponseHeader('etag');
					} 
				};
				xhr.onerror = function() {
					
				};
				xhr.send(fileObj);
  }
  
  Vue.prototype.$getPartnerLogin = () =>{
		let partner = 17
		let openId = localStorage.getItem('openId')
		if(localStorage.getItem('improvePhone')){
			return
		}else{
  		Api.getUserPartnerLogin(partner,openId).then((res)=>{
  			if(res.q.s == 0){
  				if(res.q.isBindMobile == 0){
						sessionStorage.setItem('path', router.app._route.fullPath)
  					this.$router.push({name:'ImprovePhone'})
  				}
  			}
  		})
		}
  }
}

export default layer;


