var utils = {
  wxShareInfo:{//微信分享信息
    title: "礼物飞",
    desc: "礼物飞",
    link: 'https://api.who168.com/gift-fly/dist/#/index?fromShare=1',
    imgurl: ""
    /*imgurl: "http://ss-front.kingnzo.com/static/img/wxShare.png" // 测试*/
  },
  wxInit($this){
//	let url = location.href.split('?')[0]
  	let url = location.href.split('#')[0]
    $this.$Api.getWxConfig(17,url).then((res)=>{
//    alert("提交成功"+res);
      var config =JSON.parse(res.q.config) 
      console.log(config);
      var data = JSON.parse(res.q.config)
      
      var myappid = data.appId;
      var mynoncestr = data.nonceStr;
      var mytimestamp = data.timestamp;
      var mysignature = data.signature;

      wx.config({
        debug: false,
        appId: myappid,
        timestamp: mytimestamp,
        nonceStr: mynoncestr,
        signature: mysignature,
        jsApiList: [
          'checkJsApi',
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
//        'onMenuShareQQ',
          'chooseWXPay',
          'previewImage',
        ]
      });
      wx.ready(function(){
      	utils.iniShare(utils.wxShareInfo); //分享
      });

      wx.error(function(res){
//        console.log(res)
      });
    });
  },
	iniShare: function(params,callback){//分享到朋友圈、分享给朋友
    var title = params.title,
        desc = params.desc,
      	link = params.link,
      	imgurl = params.imgurl;

//  alert(title);
    //分享到朋友圈
    wx.onMenuShareTimeline({
      title: title,
      link: link,
      imgUrl: imgurl,
      success: function () {
      	if(callback){
      		callback()
      	}
      },
      cancel: function () {
      },
      trigger: function () {
      }
    });
    //分享给朋友
    wx.onMenuShareAppMessage({
      title: title,
      desc: desc,
      link: link,
      imgUrl: imgurl,
      success: function () {
      	if(callback){
      		callback()
      	}
      },
      cancel: function () {
      	
      },
      trigger: function () {
      	
      }
    });
    //分享给qq好友
//  wx.onMenuShareQQ({
//    title: title,
//    desc: desc,
//    link: link,
//    imgUrl: imgurl,
//    success: function () {
//    	if(callback){
//    		callback()
//    	}
//    },
//    cancel: function () {
//    }
//  })
},
  //判断是否在微信内
  isWeiXin: function() {
    var ua = window.navigator.userAgent.toLowerCase();
    if (ua.match(/MicroMessenger/i) == 'micromessenger')
      return true;
    return false;
  },
  checkLogin: function($this){
    $this.$http.get(Url + 'user/check-login/', {credentials: true}).then((res)=>{
      console.log(res);
      if (res.status == 200) {
        if (res.body.msg == false) {
          if(utils.isWeiXin())
            location.href = Url + "wechat/login/?from=user";
          else
            router.push({ path: 'userOperation'});
        }
      }
    });
  },
}

export default utils;












