<template>
	<div id="common_header">
		<div class="header-bar">
			<img class="lt-arrow" src="../../assets/arrow.png" @click="goback" v-if="$route.name=='Cashier'"/>
			<img class="lt-arrow" src="../../assets/arrow.png" @click="goBack" v-else/>
			<span class="title">{{title}}</span>
			<span class="header-link" v-if="link" @click="linkClick">{{link}}</span>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	export default {
		props:['title', 'link'],
		methods: {
			linkClick() {
				this.$emit('linkClick');
			},
			goback(){
				this.$popup({
					title:'确认离开收银台？',
					content:'超过支付时效后订单将被取消，请尽快完成支付',
					cancelText:'继续支付',
					sureText:'确认离开'
				}).then((res)=>{
					console.log(res)
					if(res=='sure'){
						this.$router.replace({name:'OrderList'})
					}
				})
			},
//			goBack(){
//      if(this.$route.query.mobile == 1){
//        backward({last:true})
//      }else{
//      	if(this.$route.query.shareBy || this.$route.query.fromShare == 1){
//      		this.$router.push({name:'Index'})
//      	}else{
//      		this.$router.back(-1)
//      	}
//      }
//			},
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";

	#common_header{
		width:100%;
		height: 1.17rem;
		.header-bar{
			@include headerStyle;
			
			.lt-arrow{
				/*@include lt-arrow;*/
				display: inline-block;
				@include lt-arrow;
			}
			.title{
				font-size: 0.48rem;
			}			
			/*.edit{
				position: absolute;
				right: 0.4rem;
				font-size: 0.4rem;
			}*/
			.header-link {
				position: absolute;
				font-size: 0.4rem;
				right: 0.4rem;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
</style>


